#news-section {
  background-color: #f0f0f0;
  padding: 40px 20px;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.newsHeading {
  text-align: center;
  margin-bottom: 0px;
  color: #333;
  font-weight: 200;
  font-size: 23px;
  padding: 10px 20px;
  border-radius: 0px;
}

.newsHeading .text {
  font-size: 20px;
  font-weight: 500;
}

.slider {
  padding: 0 20px;
}

.card {
  max-width: 430px;
  height: 380px;
  margin: 20px auto;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.media {
  height: 200px;
}

.readMoreButton {
  justify-content: center;
  padding: 10px 0;
  background-color: #333;
  color: #fff;
  border-radius: 0 0 8px 8px;
  opacity: 1;
  transition: background-color 0.3s, transform 0.3s;
}

.readMoreButton Button {
  color: #fff;
  font-weight: 500;
  text-transform: uppercase;
}

.readMoreButton Button:hover {
  color: #ff5722;
  transform: scale(1.1);
}

